export function getWorkspace(data) {
  let rawWorkspace = getRawWorkspace();

  rawWorkspace.exportTime = moment().format("YYYY-MM-DD hh:mm:ss");
  rawWorkspace.containerVersion.container.publicId = data.container;

  data.tags.forEach(function (tag) {
    if (!tag.active) {
      // remove unused tags
      tag.tags.forEach(tagName => {
        const index = rawWorkspace.containerVersion.tag.findIndex(el => el.name === tagName);
        if (index !== -1) {
          rawWorkspace.containerVersion.tag.splice(index, 1);
        }
      });

      // remove unused variables
      tag.variables.forEach(variableName => {
        const index = rawWorkspace.containerVersion.variable.findIndex(el => el.name === variableName);
        if (index !== -1) {
          rawWorkspace.containerVersion.variable.splice(index, 1);
        }
      });
    } else {
      // set variables value
      tag.inputs.forEach(field => {
        const index = rawWorkspace.containerVersion.variable.findIndex(el => el.name === field.variable);
        if (index !== -1) {
          rawWorkspace.containerVersion.variable[index].parameter[0].value = field.value;
        }
      });
    }
  });

  return rawWorkspace;
}
