<template>
  <v-card class="mb-6" v-show="tag.active">
    <v-card-title>
      <v-icon
          large
          left
          :color="tag.color"
      >
        mdi-{{ tag.mdi }}
      </v-icon>
      {{ tag.name }}
    </v-card-title>
    <v-card-text>
      <v-form>
        <v-container>
          <v-row>
            <v-col cols="12" v-for="(input, index) in tag.inputs" :key="index" class="pa-0">
              <v-text-field
                  v-model="input.value"
                  :label="input.label"
                  outlined
              ></v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </v-card-text>
    <template
        v-if="tag.sponsor"
    >
      <v-row
          class="mx-6 pb-3 align-center"
      >
        <v-col cols="12">
          <v-card-title class="pl-0 pb-0">Tag Sponsor</v-card-title>
        </v-col>
        <v-col cols="3">
          <v-avatar
              rounded
              size="100"
          >
            <img
                :src="tag.sponsor.img"
                :alt="tag.sponsor.name"
            >
          </v-avatar>
        </v-col>
        <v-col cols="9">
          <p>
            <strong>{{ tag.sponsor.name }}</strong><br>
            <span class="text--secondary">{{ tag.sponsor.description }}</span>
          </p>
          <p>
            <a :href="tag.sponsor.link" target="_blank">{{ tag.sponsor.link }}</a>
          </p>
        </v-col>
      </v-row>
    </template>
    <template>
      <v-alert
          icon="mdi-star-shooting"
          type="success"
          class="rounded-0"
          prominent
          text
      >
        Want to show off your business here? Sponsor a tag.
        <v-btn
            :to="{name: 'About', hash: '#tag-sponsor'}"
            class="float-right"
            small
        >
          read more
        </v-btn>
      </v-alert>
    </template>
  </v-card>
</template>

<script>
export default {
  name: "TagForm",
  props: {
    tag: {
      type: Object,
      required: true,
    },
  },
}
</script>
