<template>
  <v-card class="mb-3">
    <v-card-title class="pa-1">
      <v-icon
          large
          left
          :color="tag.color"
      >
        mdi-{{ tag.mdi }}
      </v-icon>
      <v-switch class="float-left" inset v-model="active" @change="$emit('switch', tag.name)"></v-switch>
      {{ tag.name }}
    </v-card-title>
  </v-card>
</template>

<script>
export default {
  name: "TagCard",
  props: {
    tag: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      active: this.tag.active,
    };
  },
}
</script>
