<template>
  <v-main class="pa-0 mt-6">
    <v-row>
      <v-col cols="12" md="4">
        <v-card class="mb-3">
          <v-card-title class="pa-1">
            <v-icon
                large
                left
                color="blue"
            >
              mdi-tag-multiple
            </v-icon>
            Google Tag Manager
          </v-card-title>
          <v-card-text>
            <v-form>
              <v-container>
                <v-row>
                  <v-col cols="12" class="pa-0">
                    <v-text-field
                        v-model="data.container"
                        label="Container ID"
                        placeholder="GTM-XXXXXXX"
                        outlined
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-form>
          </v-card-text>
        </v-card>

        <tag-card v-for="(tag, index) in data.tags" :key="index" :tag="tag" @switch="onCardSwitch"></tag-card>

        <div class="text-center text--secondary text-uppercase my-6">
          <v-icon
              large
          >
            mdi-tag-arrow-down
          </v-icon>
          <br>
          More tags in development
        </div>

        <v-alert
            icon="mdi-star-shooting"
            type="success"
            prominent
            text
        >
          Need a new tag ASAP? Sponsor one.
          <v-btn
              :to="{name: 'About', hash: '#tag-sponsor'}"
              class="float-right"
              small
          >
            read more
          </v-btn>
        </v-alert>
      </v-col>

      <v-col cols="12" md="5">
        <v-alert
            border="left"
            elevation="1"
            type="info"
            v-show="activeTags <= 0"
        >
          Activate tags on the left to configure and add them to your workspace.
        </v-alert>

        <v-alert
            type="error"
            dense
            border="left"
            colored-border
            v-for="(error, index) in errors"
            :key="'error-' + index"
        >
          <div v-html="error"></div>
        </v-alert>

        <tag-form v-for="(tag, index) in data.tags" :key="index" :tag="tag"></tag-form>

        <v-textarea
            background-color="white"
            filled
            rows="10"
            placeholder="Your generated workspace will appears here..."
            readonly
            :value="textWorkspace"
        ></v-textarea>

        <div class="text-right">
          <v-btn
              plain
              color="primary"
              :href="`data:application/json;charset=utf-8,` + encodeURIComponent(textWorkspace)"
              download="workspace.json"
              :disabled="!textWorkspace"
          >
            Download as a file
          </v-btn>
        </div>
      </v-col>

      <v-col cols="12" md="3">
        <v-btn
            block
            large
            color="primary"
            :disabled="!canGenerate"
            @click="generateWorkspace"
            class="mb-6"
        >
          Generate workspace.json
        </v-btn>

        <v-card
            elevation="0"
            outlined
            class="mb-6"
        >
          <v-card-text>
            <Share></Share>
          </v-card-text>
        </v-card>

        <div class="text-center" style="margin-bottom: -13px;">
          <v-icon
              large
              color="orange"
          >
            mdi-heart
          </v-icon>
        </div>

        <v-sheet
            color="orange"
            rounded
            class="pa-3 mb-6"
        >
          <p class="text-center text--secondary ma-0">
            If this platform has been helpful to you in any way, please consider making a donation.
            This will keep the application up and running and will help many stray animals in Cuba.<br>
            Remember to include your email for tips and recommendations, I will get in touch ASAP.
            <br>
            <v-icon small>
              mdi-cat
            </v-icon>
            <v-icon small>
              mdi-heart
            </v-icon>
            <v-icon small>
              mdi-dog
            </v-icon>
          </p>
        </v-sheet>

        <div class="text-center mb-4">
          <Donate></Donate>
        </div>

        <v-divider class="mb-6 orange"></v-divider>
      </v-col>
    </v-row>
  </v-main>
</template>

<script>
import TagCard from "@/components/TagCard";
import TagForm from "@/components/TagForm.vue";
import {getWorkspace} from "@/services/workspace-service";
import Donate from "@/components/Donate.vue";
import Share from "@/components/Share.vue";

export default {
  name: 'Home',
  title: 'App',
  components: {Share, Donate, TagForm, TagCard},
  data() {
    return {
      errors: [],
      data: {
        container: "",
        tags: [
          {
            name: "Google Analytics Universal",
            subtitle: "The Google Analytics you're used to.",
            active: false,
            color: "orange",
            mdi: "google-analytics",
            inputs: [
              {
                label: "UA Tracking ID",
                value: "",
                variable: "Google UA Tracking ID",
              },
              {
                label: "Google Optimize Container ID",
                value: "",
                variable: "Google Optimize Container ID",
              },
            ],
            tags: ["Google UA - All Pages", "Google Optimize UA"],
            variables: ["Google UA Tracking ID", "Google Universal Analytics"],
            sponsor: null,
          },
          {
            name: "Google Analytics 4",
            subtitle: "The new Google Analytics.",
            active: false,
            color: "orange",
            mdi: "google-analytics",
            inputs: [
              {
                label: "GA4 Tracking ID",
                value: "",
                variable: "Google GA4 Tracking ID",
              },
              {
                label: "Google Optimize Container ID",
                value: "",
                variable: "Google Optimize Container ID",
              },
            ],
            tags: ["Google A4 - All Pages", "Google Optimize GA4"],
            variables: ["Google GA4 Tracking ID", "Google Analytics 4"],
            sponsor: null,
          },
        ],
      },
      workspace: {},
      jsonWorkspace: "",
    };
  },
  computed: {
    activeTags: function () {
      let activeTags = this.data.tags.filter(function (el) {
        return el.active;
      });

      return activeTags.length;
    },
    textWorkspace: function () {
      return JSON.stringify(this.workspace);
    },
    canGenerate() {
      return (this.activeTags > 0 && this.data.container !== "");
    },
  },
  methods: {
    onCardSwitch(tagName) {
      this.data.tags.forEach(function (part, index, arr) {
        if (arr[index].name === tagName) {
          arr[index].active = !arr[index].active;
        }
      });
    },
    isValid() {
      let errors = [];

      // validate tags
      this.data.tags.forEach(function (tag) {
        if (tag.active) {
          tag.inputs.forEach(function (field) {
            if (field.value === "") {
              errors.push(`Field <em>${field.label}</em> of <strong>${tag.name}</strong> tag is mandatory.`);
            }
          });
        }
      });

      this.errors = errors;

      return errors.length === 0;
    },
    generateWorkspace() {
      if (this.isValid()) {
        this.workspace = getWorkspace(this.data);
        this.$gtm.trackEvent({
          action: 'click',
          event: 'generate_workspace',
          label: 'Generate Workspace',
        });
        //console.log(this.workspace);
      }
    },
  },
}
</script>
